.essay {
    width: 80%;
    height: 85vh;
    float: right;
    position: relative;
  }
.img-box{
    position: relative;
}
.img-bottom{
    position: absolute;
    background-color: rgb(94, 61, 184);
    width: 15vw;
    font-size: 1.4vw;
    text-align: center;
    color: rgb(194, 187, 84);
}
.img-title{
    /* position: absolute; */
    background-color: rgb(94, 61, 184);
    width: 15vw;
    color: #1DA57A;
    font-size: 1.5vw;
    text-align: center;
}
.img-item{
    width: 15vw;
    height: 15vw;
}
.img-bottom a:nth-child(1){
display: inline-block;
position: absolute;
left: 0;
color: rgb(194, 187, 84);
}
.img-bottom a:nth-child(3){
    display: inline-block;
    position: absolute;
    right: 0;
    color: rgb(194, 187, 84);
}
@media screen and (max-width:1000px){
    .essay {
        width: 100%;
        /* position: relative; */
        /* left: 10vw; */
        padding-left: 5vw;
        padding-top: 2vh;
      }
      .img-item{
        width: 30vw;
        height: 30vw;
    }
    .img-title{
        width: 30vw;
        font-size: 2.3vw;
    }
    .img-bottom{
        width: 30vw;
        font-size: 2vw;
    }
}